<template lang="pug">
.uploadFile
  b-icon-link45deg(@click="uploadFile") 上傳檔案
  input(
    ref="uploader"
    class="d-none"
    type="file"
    @change="onFileChanged"
  )
</template>

<script>
import heic2any from "heic2any";

export default {
  props: {
    maxSize: {
      type: Number,
      default: 10240000 // 10mb
    },
    limitType: {
      type: [Array, String],
      default: () => ['image/png', 'image/gif', 'image/jpg', 'image/jpeg']
    }
  },
  data() {
    return {
      file: null, // 更新圖片的formData
    }
  },
  methods: {
    uploadFile() {
      console.log('上傳檔案')
      this.$refs.uploader.click();
    },
    onFileChanged(e) {
      const vm = this;

      if(e.target.files[0]) {
        if(e.target.files[0].size > 10240000) {
          vm.$_errorHandler(402)
          return;
        }
        vm.file = e.target.files[0];

        if(vm.isHeic(vm.file)) { // ios圖檔類型
          console.log('isHeic')
          heic2any({ blob: vm.file, toType: "image/jpeg", quality: 0.1 })
          .then(function (resultBlob) {
            resultBlob.name = vm.file.name.split(".")[0] + ".jpeg";
            vm.file = resultBlob;
            vm.$emit('finish', resultBlob);
            vm.clearFileInput();
          })
        } else {
          vm.$emit('finish', vm.file);
          vm.clearFileInput();
        }
      }
    },
    isHeic: function (file) {
      const name = file.name.toLowerCase() || file.src.toLowerCase();
      return name.includes('heic') || name.includes('heif');
    },
    clearFileInput() {
      this.$refs.uploader.value = null;
    }
  }
};
</script>
<style lang="scss" scoped>
.uploadFile {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #007bff;
  transition: 150ms ease-in-out;
  svg {
    padding: 4px;
    width: 36px;
    height: 36px;
    cursor: pointer;
  }
  &:hover {
    color: #0062cc;
  }
}
</style>